.container {
    display: flex;
    margin: 4rem 10rem 2rem 8rem ;
    padding-bottom: 3rem;
    
    
}
body{
    overflow-x: hidden;
}
.container-img {
    width: 400px;
    height: 380px;
    padding-top: 6.5rem;

}

.descr-one{
    margin-left: 6.5rem;
}
.descr-one h1{
 font-size: 2.8rem;
}
.descr-one p {
    font-size: 1.2rem;
    font-weight: 350;
    margin-right: 8rem;
    padding-top: 1.5rem;
    color: #fff;
   }

   .meeting-schedule {
    padding: 0.5rem;
    margin-top: 2rem;
    background-color: transparent;
    font-weight: 300;
    font-size: 1.2rem;
    border-radius: 16px;
    border: 2px solid #ffffff ;
    color: white;
   }

   .meeting-schedule:hover{
    background: #217079;;
    color: #fff;
    transition: ease-in 0.3s;
    cursor: pointer;

}

.mob-version {
    display: none;
}

@media screen and (max-width: 1040px) {
    .container {
        display: flex;
        flex-direction: column;
        align-items: center;
        /* border: 1px solid red; */
        margin: 2rem 2rem 2rem 2rem ;
        padding-bottom: 1.5rem;
        
        
    }

    .mob-version {
        display: inline;
    }
    
    body{
        overflow-x: hidden;
    }
    .container-img {
        display: none;
        width: 15rem;
        height: 180px;
        padding-top: 1.5rem;
    
    }
    
    .descr-one{
        margin-left: 1rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;


    }
    .descr-one h1{
     font-size: 2.2rem;
     text-align: center;

    }

    .title__two {
        padding-left: 3rem;
    }
    .descr-one p {
        font-size: 1.3rem;
        text-align: center;
        font-weight: 350;
        margin-right: 0rem;
        padding-top: 0.5rem;
        padding-left: 1.5rem;
        padding-right: 1.5rem;

        color: #fff;
       }
    
       .meeting-schedule {
        padding: 0.5rem;
        margin-top: 2rem;
        background-color: transparent;
        font-weight: 300;
        font-size: 1.2rem;
        border-radius: 16px;
        border: 2px solid #ffffff ;
        color: white;
       }
    
       .meeting-schedule:hover{
        background: #217079;;
        color: #fff;
        transition: ease-in 0.3s;
        cursor: pointer;
    
    }
}
