
.tracking__wrapper {
    background-color: #f3f3f3;
    padding: 5%;
}
.htag {
    display: flex;
    color: black;
    font-weight: 600;
    font-size: large;
}
 p{
    color: rgb(112, 114, 112);
 }
 .fcolor{
    color: black;
    margin-bottom: 3rem;
    font-size: xx-large;
 }
.parent {
    display: grid ;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(5, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    margin: 4rem 10rem 2rem 10rem;
    height: calc(100vh - 200px);
    width: calc(100vw - 400px);
    
    }
    
    .div1 { grid-area: 1 / 1 / 2 / 3; }
    .div2 { 
            grid-area: 2 / 1 / 3 / 2;
            display: flex;
                       
    }
    .flt1{
        height: 3rem;
        margin-right: 1rem;
    }
    .div3 { 
            grid-area: 2 / 2 / 3 / 3;
            display: flex;
        
        }
    .div4 { grid-area: 3 / 1 / 4 / 2;
        display: flex;
            
    }
    .div5 { grid-area: 3 / 2 / 4 / 3; }
    .div6 { 
        grid-area: 4 / 1 / 5 / 3;
        height: fit-content;
        
    }
    .div7 { grid-area: 5 / 1 / 6 / 2; }
    .div8 { grid-area: 5 / 2 / 6 / 3; }
    .div9 { grid-area: 1 / 3 / 6 / 4;
            
    }

    .cntbutton {
    padding: 2.5% 6%;
    font-size: 0.8rem;
    background: transparent;
    color: #013237;
    border: 2px solid #013237;
    font-weight: bold;
    cursor: pointer;
    border-radius: 8px;
    
    }

    .cntbutton:hover{
        background: #013237;
        color: #fff;
        transition: ease-in 0.3s;
    }


    @media screen and (max-width: 1040px) {
        .div9 { 
            display: none;
    }
    .tracking__wrapper {
        display: flex;
        z-index: 5;
        background-color: #f3f3f3;
        border: 1px solid red;
        padding: 1rem;
    }
    .htag {
        display: flex;
        color: black;
        font-weight: 600;
        font-size: large;
    }
     p{
        color: rgb(112, 114, 112);
     }
     .fcolor{
        color: black;
        margin-bottom: 2rem;
        font-size: x-large;
     }
